import Loading from './brick/Loading'
import TitleBar from './brick/Title'
import BottomBar from './brick/Bottom'
import P42 from './panel/P42'
import {post, Timestamp} from "./method.js"
import { Order } from './Order.js'
import localforage from 'localforage'
export default {
  components: {
    TitleBar,
    BottomBar,
    Loading,
    P42,
  },
  methods: {
    quitApp: function(url) { //退出登录
      const _this = this;
      Order.$emit('Loading', 'show')
      new QWebChannel(navigator.qtWebChannelTransport, function(channel) {
        const foo = channel.objects.content;
        foo.callback.connect(function(receive) {
          //隐藏退出提示
          Order.$emit('Loading', 'hide')
          //收到消息就返回主界面
          window.location.href="#/Quit";
        });
        foo.loginout()
      });
    }
  },
  created(){
    localforage.getItem("appData",(err,appData) =>{
      const nowTime = new Date().getTime()
      if(nowTime - Timestamp.value > 1200000){
        window.location.href="#/TimeOut";
        return null
      }
      Timestamp.value = nowTime
      this.userName = appData.userData.userName
    })
  },
  data () {
    return {
      list: [
        { icon: '&#xe600;', title: '设置', color:'#61c2ff', id:"1000",url:"/Sett"}, 
        { icon: '&#xe629;', title: '帮助', color:'#ffd217', id:"1001",url:"/Help"},
        { icon: '&#xe60e;', title: '当前版本', color:'#1bee47', id:"1002",url:"/Version"}
      ],
      visible: true,
      list1: [['小米', 'iPhone', '华为', '情怀', '三星', '其他', '不告诉你']],
      value1: ['iPhone'],
      userName: '',
      sexList:["未填写","男","女"],
      sex:""
    }
  }
}



